.profile {
  max-width: 880px;
  margin: 40px auto 51px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

@media screen and (max-width: 1023px) {
  .profile {
    max-width: 282px;
    flex-direction: column;
    margin-top: 42px;
    margin-bottom: 36px;
  }
}
